import React, { Component } from 'react'
import { RiFolderSettingsFill } from 'react-icons/ri';
import Courses from './Courses/Courses';
import EducationCategories from './EducationCategories/EducationCategories';
import QuestionTypes from './QuestionTypes/QuestionTypes';
import SchoolLevels from './SchoolLevels/SchoolLevels';

enum SettingsType {
  questionTypes = "questionTypes",
  courses = "courses",
  educationCategory = "educationCategory",
  schoolLevels = "schoolLevels",
  chapters = "chapters",
}

interface QuestionSettingsState {
  loading: boolean;
  settings: {
    settingType: SettingsType;
    caption: string;
  }[],
  selectedSetting: SettingsType | null;
}
interface QuestionSettingsProps {}

export class QuestionSettings extends Component<QuestionSettingsProps, QuestionSettingsState> {
  constructor(props: QuestionSettingsProps) {
    super(props);

    this.state = {
      loading: true,
      settings: [
        {
          settingType: SettingsType.questionTypes,
          caption: "Question Types"
        },
        {
          settingType: SettingsType.courses,
          caption: "Courses"
        },
        {
          settingType: SettingsType.educationCategory,
          caption: "Education categories"
        },
        {
          settingType: SettingsType.schoolLevels,
          caption: "School levels"
        },
      ],
      selectedSetting: SettingsType.educationCategory,
    };
  }
  render() {
    return (
      <div className="">
        <div className="mt-3 rounded flex flex-row justify-between items-center">
          <div className="flex flex-row items-center gap-3">
            <RiFolderSettingsFill className="text-6xl text-blue-600" />
            <div className="flex flex-col">
              <span className="text-blue-600 text-2xl font-bold">Question Bank settings</span>
              <span className="text-gray-600 text-sm">Customization of question bank predefined settings including courses, chapters, question types, etc</span>
            </div>
          </div>
          <div className="flex flex-row">
            {/* <Link to="/method-add-question" className={`btn bg-blue-100 hover:bg-white text-blue-700 border-2 border-white`}>Add questions</Link> */}
            {/* <div className="btn bg-black bg-opacity-30 text-white border-2 border-white">Change course</div> */}
          </div>
        </div>
        <div className="mt-3 flex-col">
          <div className="flex flex-col">
            <div className="bg-white flex rounded mb-20">
              <div className="bg-white w-full border border-white rounded">
                <div className="flex flex-row items-center">
                  {this.state.settings.map((item, i) => (
                    <div onClick={() => this.setState({ selectedSetting: item.settingType })} className={`animate__animated ${this.state.selectedSetting === item.settingType ? "bg-blue-100 border-blue-600 text-blue-600 animate__zoomIn" : "bg-white border-white text-gray-600"} hover:bg-blue-100 px-4 py-2 cursor-pointer font-bold border-b-4 hover:border-blue-600 hover:text-blue-600 rounded-t mt-1 mx-1`}>
                      {item.caption}
                    </div>
                  ))}
                </div>
                <div className="=rounded mx-1 mb-1">
                  {this.state.selectedSetting === SettingsType.questionTypes && <QuestionTypes />}
                  {this.state.selectedSetting === SettingsType.educationCategory && <EducationCategories />}
                  {this.state.selectedSetting === SettingsType.schoolLevels && <SchoolLevels />}
                  {this.state.selectedSetting === SettingsType.courses && <Courses />}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
