import React, { Fragment, useEffect, useState } from 'react'
import { MdEdit } from 'react-icons/md'
import { CgDetailsMore } from 'react-icons/cg'
import { HiOutlineDocumentSearch } from 'react-icons/hi'
import { connect } from "react-redux";
import {
  Auth,
  loadApplicationPeriods,
  setSelectedPeriod,
  applicationPeriodsStoreInterface,
  schoolLevelsStoreInterface,
  loadEducationCategories,
  setSelectedEducationCategory,
  loadSchoolLevels,
  System,
  PushPopupAlert,
} from "../../../../actions";
import Loading from '../../../../components/Loading/Loading';
import { StoreState } from "../../../../reducers";
import Modal, { ModalSize, Themes } from '../../../../components/Modal/Modal';
import { PopupAlertData } from '../../../../components/Alert/Popup';
import { IoMdTrash } from 'react-icons/io';
import axios from 'axios';
import { API } from '../../../../utils/api';
import { PopupAlertTheme } from '../../../../interfaces/popupAlert';
import { education_category } from '../../../../interfaces/education_category';
import AddEducationCategory from './AddEducationCategory';

interface EducationCategoryPropsInterface {
  system: System;
  schoolLevels: schoolLevelsStoreInterface;
  PushPopupAlert: (alert: PopupAlertData) => void;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  loadSchoolLevels: (callback: (loading: boolean) => void) => void;
}

export enum EducationCategoryTypeAction {
  Create = "Create",
  View = "View",
  Delete = "Delete",
  Update = "Update",
}

export interface E_EducationCategoryDetails {
  action: EducationCategoryTypeAction;
  selectedEducationCategory: education_category;
}

const _EducationCategories = (props: EducationCategoryPropsInterface) => {
  const [loading, setLoading] = useState<boolean>(false);
  const [addNew, setAddNew] = useState<boolean>(false);
  const [selectedEducationCategory, setSelectedEducationCategory] = useState<E_EducationCategoryDetails | null>(null);

  const closeAddNewModal = (state: boolean) => {
    setAddNew(state);
    props.loadEducationCategories(setLoading);
  }

  const deleteEducationCategoryFn = async (education_category: education_category) => {
    if (education_category.education_institute_id !== undefined) {
      setLoading(true);
      try {
        const res = await axios.delete(`${API.APPLICATION}/educationinstitute/${education_category.education_institute_id}`);
        props.loadEducationCategories(setLoading);
        props.PushPopupAlert({
          title: `Education category removed successfully`,
          theme: PopupAlertTheme.success,
        });
        setSelectedEducationCategory(null);
        console.log("Deleted: ", res);
      } catch (error) {
        console.log("err: ", error);
      }
      // setLoading(false);
    }
  }

  useEffect(() => {
    if (props.schoolLevels.education_categories === null || props.schoolLevels.education_categories.length === 0) {
      props.loadEducationCategories(setLoading);
    }
    if (props.schoolLevels.school_levels === null || props.schoolLevels.school_levels.length === 0) {
      props.loadSchoolLevels(setLoading);
    }
  }, [props, setLoading]);
  return (
    <Fragment>
      <div className="mt-1">
        <div className="flex flex-row justify-between items-center bg-gray-100 py-1">
          <div className="text-blue-600 font-extrabold text-xl px-2">Lists of education categories</div>
          {loading === false &&<div onClick={() => setAddNew(true)} className="btn bg-blue-600 text-white cursor-pointer hover:bg-blue-800">Add new</div>}
        </div>
        <div className="flex flex-col">
          {props.schoolLevels.education_categories === null || loading === true ? <Loading /> :
          props.schoolLevels.education_categories.length === 0 ?
          <div className="text-gray-400 px-6 py-4 rounded border mt-4 text-xl font-extrabold flex flex-col items-center justify-center bg-gray-100">
            <HiOutlineDocumentSearch className="text-7xl text-gray-400 animate__animated animate__shakeX" />
            <span>No education categories available</span>
          </div> : 
          props.schoolLevels.education_categories.map((item, i) => (
            <div className={`border flex flex-row px-3 py-2 rounded items-center my-1 justify-between`}>
              <div className="flex flex-col">
                <span className="text-gray-600 font-extrabold">{item.education_category_title}</span>
                <span className={`text-xs pr-5 text-gray-500`}>{item.description}</span>
              </div>
              <div className="flex flex-row items-center gap-2">
                <div 
                onClick={() => setSelectedEducationCategory({ 
                  action: EducationCategoryTypeAction.View,
                  selectedEducationCategory: item
                })} 
                className="bg-gray-50 hover:bg-gray-200 rounded-full px-1 py-1 cursor-pointer">
                  <CgDetailsMore className="text-3xl text-gray-700"/>
                </div>
                <div 
                onClick={() => setSelectedEducationCategory({ 
                  action: EducationCategoryTypeAction.Update,
                  selectedEducationCategory: item
                })} 
                className="bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 cursor-pointer">
                  <MdEdit className="text-3xl text-blue-700"/>
                </div>
                <div 
                onClick={() => setSelectedEducationCategory({ 
                  action: EducationCategoryTypeAction.Delete,
                  selectedEducationCategory: item
                })} 
                className="bg-gray-100 hover:bg-red-100 rounded-full px-1 py-1 cursor-pointer">
                  <IoMdTrash className="text-3xl text-yellow-700"/>
                </div>
              </div>
              
            </div>
          ))}
        </div>
      </div>
      {(addNew === true ||
      (selectedEducationCategory !== null && 
      (selectedEducationCategory.action === EducationCategoryTypeAction.Update || selectedEducationCategory.action === EducationCategoryTypeAction.View)
      )) &&
      <Modal
        title={<div className="flex flex-row gap-4 items-center">
          <div className="flex flex-col">
            <span className="font-extrabold text-2xl text-gray-500">
              {selectedEducationCategory !== null && selectedEducationCategory.action === EducationCategoryTypeAction.Update ? selectedEducationCategory.selectedEducationCategory.education_category_title : selectedEducationCategory !== null && selectedEducationCategory.action === EducationCategoryTypeAction.View ? selectedEducationCategory.selectedEducationCategory.education_category_title : "Add new education category"}
            </span>
            <span className="text-sm text-gray-500 font-normal">
              {selectedEducationCategory !== null && selectedEducationCategory.action === EducationCategoryTypeAction.Update ? "Update the selected education category" : selectedEducationCategory !== null && selectedEducationCategory.action === EducationCategoryTypeAction.View ? "View more details for the selected education category" : "An education category will be used in different components in the system, make sure what you're creating"}
            </span>
          </div>
        </div>}
        backDrop={true}
        theme={Themes.default}
        close={() => {
          setAddNew(false);
          setSelectedEducationCategory(null);
        }}
        widthSizeClass={ModalSize.extraLarge}
        displayClose={true}
        backDropClose={true}
        padding={{title: true, body: true }} >
          <AddEducationCategory 
            closeModal={closeAddNewModal}
            education_categories={props.schoolLevels}
            PushPopupAlert={props.PushPopupAlert}
            selectedEducationCategory={selectedEducationCategory}
            deleteSelected={setSelectedEducationCategory}
            schoolLevels={props.schoolLevels}
          />
      </Modal>}
      {selectedEducationCategory !== null && selectedEducationCategory.action === EducationCategoryTypeAction.Delete &&
      <Modal
        title={<div className="flex flex-row gap-4 items-center">
          <div className="flex flex-col">
            <span className="font-extrabold text-2xl text-gray-500">{selectedEducationCategory.selectedEducationCategory.education_category_title}</span>
          </div>
        </div>}
        backDrop={true}
        theme={Themes.default}
        close={() => setSelectedEducationCategory(null)}
        widthSizeClass={ModalSize.medium}
        displayClose={true}
        backDropClose={true}
        padding={{title: true, body: true }} >
          <div className="-mt-6">
            <span className="text-red-600 font-bold">
              Are you sure do you want to delete this education category?
            </span>
            <div className="flex flex-row items-center justify-between mt-2">
              <div onClick={() => setSelectedEducationCategory(null)} className="btn bg-gray-100 text-gray-700 cursor-pointer hover:bg-gray-200">Cancel</div>
              <div onClick={() => deleteEducationCategoryFn(selectedEducationCategory.selectedEducationCategory)} className="btn bg-red-600 text-white cursor-pointer hover:bg-red-800">Yes, Delete</div>
            </div>
          </div>
      </Modal>
      }
    </Fragment>
  )
}


const mapStateToProps = ({
  auth,
  periods,
  schoolLevels,
  system,
}: StoreState): { auth: Auth; periods: applicationPeriodsStoreInterface, schoolLevels: schoolLevelsStoreInterface; system: System; } => {
  return { auth: auth, periods: periods, schoolLevels: schoolLevels, system: system };
};

const EducationCategories = connect(mapStateToProps, {
  loadApplicationPeriods,
  setSelectedPeriod,
  loadEducationCategories,
  setSelectedEducationCategory,
  PushPopupAlert,
  loadSchoolLevels,
})(_EducationCategories);

export default EducationCategories;