import React, { Fragment, useEffect, useState } from 'react'
import { MdEdit } from 'react-icons/md'
import { CgDetailsMore } from 'react-icons/cg'
import { HiOutlineDocumentSearch } from 'react-icons/hi'
import { connect } from "react-redux";
import {
  Auth,
  loadApplicationPeriods,
  setSelectedPeriod,
  applicationPeriodsStoreInterface,
  schoolLevelsStoreInterface,
  loadSchoolLevels,
  setSelectedEducationCategory,
  System,
  PushPopupAlert,
} from "../../../../actions";
import Loading from '../../../../components/Loading/Loading';
import { StoreState } from "../../../../reducers";
import Modal, { ModalSize, Themes } from '../../../../components/Modal/Modal';
import AddQuestionType from './AddSchoolLevel';
import { PopupAlertData } from '../../../../components/Alert/Popup';
import { IoMdTrash } from 'react-icons/io';
import axios from 'axios';
import { API } from '../../../../utils/api';
import { PopupAlertTheme } from '../../../../interfaces/popupAlert';
import { school_level } from '../../../../interfaces/school_level';

interface SchoolLevelsPropsInterface {
  system: System;
  schoolLevels: schoolLevelsStoreInterface;
  PushPopupAlert: (alert: PopupAlertData) => void;
  loadSchoolLevels: (callback: (loading: boolean) => void) => void;
}

export enum SchoolLevelsAction {
  Create = "Create",
  View = "View",
  Delete = "Delete",
  Update = "Update",
}

export interface S_SchoolLevelDetails {
  action: SchoolLevelsAction;
  selectedSchoolLevel: school_level;
}

const _SchoolLevels = (props: SchoolLevelsPropsInterface) => {
  const [loading, setLoading] = useState<boolean>(false);
  const [addNew, setAddNew] = useState<boolean>(false);
  const [selectedSchoolLevel, setSelectedSchoolLevel] = useState<S_SchoolLevelDetails | null>(null);

  const closeAddNewModal = (state: boolean) => {
    setAddNew(state);
    props.loadSchoolLevels(setLoading);
  }

  const deleteEducationCategoryFn = async (school_level: school_level) => {
    if (school_level.school_level_id !== undefined) {
      setLoading(true);
      try {
        const res = await axios.delete(`${API.APPLICATION}/schoollevel/${school_level.school_level_id}`);
        props.loadSchoolLevels(setLoading);
        props.PushPopupAlert({
          title: `School level removed successfully`,
          theme: PopupAlertTheme.success,
        });
        setSelectedSchoolLevel(null);
        console.log("Deleted: ", res);
      } catch (error: any) {
        props.PushPopupAlert({
          title: `Acton failed, Try again later!`,
          theme: PopupAlertTheme.warning,
        });
        setLoading(false);
        setSelectedSchoolLevel(null);
        console.log("err: ", {...error});
      }
      // setLoading(false);
    }
  }

  useEffect(() => {
    if (props.schoolLevels.school_levels === null || props.schoolLevels.school_levels.length === 0) {
      props.loadSchoolLevels(setLoading);
    }
  }, [props, setLoading]);
  return (
    <Fragment>
      <div className="mt-1">
        <div className="flex flex-row justify-between items-center bg-gray-100 py-1">
          <div className="text-blue-600 font-extrabold text-xl px-2">Lists of school levels</div>
          {loading === false &&<div onClick={() => setAddNew(true)} className="btn bg-blue-600 text-white cursor-pointer hover:bg-blue-800">Add new</div>}
        </div>
        <div className="flex flex-col">
          {props.schoolLevels.school_levels === null || loading === true ? <Loading /> :
          props.schoolLevels.school_levels.length === 0 ?
          <div className="text-gray-400 px-6 py-4 rounded border mt-4 text-xl font-extrabold flex flex-col items-center justify-center bg-gray-100">
            <HiOutlineDocumentSearch className="text-7xl text-gray-400 animate__animated animate__shakeX" />
            <span>No question types available</span>
          </div> : 
          props.schoolLevels.school_levels.map((item, i) => (
            <div className={`border flex flex-row px-3 py-2 rounded items-center my-1 justify-between`}>
              <div className="flex flex-col">
                <span className="text-gray-600 font-extrabold">{item.school_level_name}</span>
                <span className={`text-xs pr-5 text-gray-500`}>{item.description}</span>
              </div>
              <div className="flex flex-row items-center gap-2">
                <div 
                onClick={() => setSelectedSchoolLevel({ 
                  action: SchoolLevelsAction.View,
                  selectedSchoolLevel: item
                })} 
                className="bg-gray-50 hover:bg-gray-200 rounded-full px-1 py-1 cursor-pointer">
                  <CgDetailsMore className="text-3xl text-gray-700"/>
                </div>
                <div 
                onClick={() => setSelectedSchoolLevel({ 
                  action: SchoolLevelsAction.Update,
                  selectedSchoolLevel: item
                })} 
                className="bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 cursor-pointer">
                  <MdEdit className="text-3xl text-blue-700"/>
                </div>
                <div 
                onClick={() => setSelectedSchoolLevel({ 
                  action: SchoolLevelsAction.Delete,
                  selectedSchoolLevel: item
                })} 
                className="bg-gray-100 hover:bg-red-100 rounded-full px-1 py-1 cursor-pointer">
                  <IoMdTrash className="text-3xl text-yellow-700"/>
                </div>
              </div>
              
            </div>
          ))}
        </div>
      </div>
      {(addNew === true ||
      (selectedSchoolLevel !== null && 
      (selectedSchoolLevel.action === SchoolLevelsAction.Update || selectedSchoolLevel.action === SchoolLevelsAction.View)
      )) &&
      <Modal
        title={<div className="flex flex-row gap-4 items-center">
          <div className="flex flex-col">
            <span className="font-extrabold text-2xl text-gray-500">
              {selectedSchoolLevel !== null && selectedSchoolLevel.action === SchoolLevelsAction.Update ? selectedSchoolLevel.selectedSchoolLevel.school_level_name : selectedSchoolLevel !== null && selectedSchoolLevel.action === SchoolLevelsAction.View ? selectedSchoolLevel.selectedSchoolLevel.school_level_name : "Add new school level"}
            </span>
            <span className="text-sm text-gray-500 font-normal">
              {selectedSchoolLevel !== null && selectedSchoolLevel.action === SchoolLevelsAction.Update ? "Update the selected school level" : selectedSchoolLevel !== null && selectedSchoolLevel.action === SchoolLevelsAction.View ? "View more details for the selected school level" : "School level will be used in different components in the system, make sure what you're creating"}
            </span>
          </div>
        </div>}
        backDrop={true}
        theme={Themes.default}
        close={() => {
          setAddNew(false);
          setSelectedSchoolLevel(null);
        }}
        widthSizeClass={ModalSize.extraLarge}
        displayClose={true}
        backDropClose={true}
        padding={{title: true, body: true }} >
          <AddQuestionType 
            closeModal={closeAddNewModal}
            school_levels={props.schoolLevels}
            PushPopupAlert={props.PushPopupAlert}
            selectedSchoolLevel={selectedSchoolLevel}
            deleteSelected={setSelectedSchoolLevel}
          />
      </Modal>}
      {selectedSchoolLevel !== null && selectedSchoolLevel.action === SchoolLevelsAction.Delete &&
      <Modal
        title={<div className="flex flex-row gap-4 items-center">
          <div className="flex flex-col">
            <span className="font-extrabold text-2xl text-gray-500">{selectedSchoolLevel.selectedSchoolLevel.school_level_name}</span>
          </div>
        </div>}
        backDrop={true}
        theme={Themes.default}
        close={() => setSelectedSchoolLevel(null)}
        widthSizeClass={ModalSize.medium}
        displayClose={true}
        backDropClose={true}
        padding={{title: true, body: true }} >
          <div className="-mt-6">
            <span className="text-red-600 font-bold">
              Are you sure do you want to delete this school level?
            </span>
            <div className="flex flex-row items-center justify-between mt-2">
              <div onClick={() => setSelectedSchoolLevel(null)} className="btn bg-gray-100 text-gray-700 cursor-pointer hover:bg-gray-200">Cancel</div>
              <div onClick={() => deleteEducationCategoryFn(selectedSchoolLevel.selectedSchoolLevel)} className="btn bg-red-600 text-white cursor-pointer hover:bg-red-800">Yes, Delete</div>
            </div>
          </div>
      </Modal>
      }
    </Fragment>
  )
}


const mapStateToProps = ({
  auth,
  periods,
  schoolLevels,
  questionTypes,
  system,
}: StoreState): { auth: Auth; periods: applicationPeriodsStoreInterface, schoolLevels: schoolLevelsStoreInterface; system: System; } => {
  return { auth: auth, periods: periods, schoolLevels: schoolLevels, system: system };
};

const SchoolLevels = connect(mapStateToProps, {
  loadApplicationPeriods,
  setSelectedPeriod,
  loadSchoolLevels,
  setSelectedEducationCategory,
  PushPopupAlert,
})(_SchoolLevels);

export default SchoolLevels;